*{
  margin: 0;
  padding: 0;
}

:root{
  --grey-color: grey;
  --red-color: red;
  --green-color: #a8ff3e;
  --blue-color: #0f4c81;
  --white-color: #ffffff;
  --background-color: #1b262c;
  --backdrop-color: #304048;

  --switch-height: 4px;
  --switch-width: 45px;

  --slider-button-width: 13px;
  --slider-button-height: 13px;
}

.world-total:lang(en){
  font-family: 'Times New Roman', Times, serif;
}

html,body{
  width: 100%;
  height: 100%;
  background: var(--background-color);
}

.daily-report{
  text-align: center;
  padding: 1em;
  width: 65%;
  margin: 0px auto;
  color: white;
}

.language-switch{
  background: var(--backdrop-color);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65px; 
}

.language-switch-khmer,
.language-switch-english{
  background: none;
  border: none;
  color: white;
  font-size: 14px;
  letter-spacing: 4px;
  cursor: pointer;
  text-transform: uppercase;
}

.language-switch-khmer:focus,
.language-switch-english:focus{
  outline: none;
}

.language-switch-khmer-active,
.language-switch-english-active{
  background: none;
  color: var(--green-color);
  font-size: 14px;
}

.switch {
  margin: 0 8px;
  position: relative;
  display: inline-block;
  width: var(--switch-width);
  height: var(--switch-height);
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--green-color);
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: var(--slider-button-height);
  width: var(--slider-button-width);
  left: 4px;
  right: 0px;
  bottom: -4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: var(--green-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--green-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX( calc(var(--switch-width) - 20.5px) );
  -ms-transform: translateX( calc(var(--switch-width) - 20.5px) );
  transform: translateX( calc(var(--switch-width) - 20.5px) );
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.daily-report-title{
  margin-top: 1em;
  margin-bottom: 0px;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 5px;
}

.daily-report-updated{
  margin-top: 10px;
  margin-bottom: 10px;
}

.data-source{
  color: #a8ff3e;
}
.data-source:link{
  text-decoration: none;
}

.data-source:visited{
  color: grey;
}
.world-total{
  letter-spacing: 3px;
  margin: 0 auto 10px;
  padding-top: 24px;
  padding-bottom: 10px;
  text-align: center;
}

.world-total-number{
  font-weight: bold;
  font-size: 2em;
  width: 50%;
  margin: auto;
  margin-top: 0px;
  border-bottom: 1px solid;
  margin-bottom: 25px;
}

.world-total-cases{
  color: grey;
}

.world-total-deaths{
  color: red;
}

.world-total-recovered{
  color: #a8ff3e;
}
.searchbar{
  margin-bottom: 20px;
}

.searchbar-button{
  display: flex;
  justify-content: space-around;
  font-size: 1.3em; 
}

.cambodia, 
.usa{
  border: none;
  width: 150px;
  height: 40px;
  font-size: 1em;
  text-transform: uppercase;
  background: white;
}

.cambodia{
  letter-spacing: 2px;
  margin-left: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.cambodia-active,
.usa-active{
    background: var(--blue-color);;
    color: white;
    font-weight: bold;
}

.usa{
  letter-spacing: 3px;
  margin-right: 10px;
}

.cambodia:active,
.usa:active{
  background: var(--blue-color);
  color: white;
}
.search-bar-textbox{
  width: 73%;
  height: 36px;
  padding-left: 10px;
  margin: 0px;
  border: none;
  font-size: 16px;
}

.search-bar-clear-button{
  height: 36px;
  width: 70px;
  margin: 0px;
  padding: 0px;
  font-size: 16px;
  line-height:8px;
  border: none;
  margin-left: 5px;
  background: white
}

.search-bar-clear-button:hover{
  background: var(--blue-color);
  color: white;
}

.search-bar-option{
  font-size: 1.2em;
  letter-spacing: 1.5px;
  margin: 15px;
}

.report-card{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 25px auto;
  width: 80%;
  color: white;
}

.report-card-header{
  text-align: center;
  background: #0f4c81;
  font-weight: bold;
  font-size: 1.8em;
  padding-top: .2em;
  padding-bottom: .2em;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.report-card-body{
  display: flex;
  line-height: 2.3em;
  background: #235952;
}

.lists{
  padding: 20px 10px 20px 70px;
  text-align: left;
  display: grid;
  grid-template-columns: 1fr auto auto;
  font-size: 1.5em;
  letter-spacing: 1px;
}

.end-section-today, .end-section-total{
  margin-bottom: 10px;
  padding-bottom: 10px;
}

.list-amount{
  padding-left: 15px;
  padding-right: 15px;
  font-weight: bold;
}

.report-card-body-flag{
  margin-left: auto;
  padding-right: 70px;
  height: 100px;
  width: 200px;
  align-self: center;
}

@media (max-width: 1275px){
  .daily-report{
    text-align: center;
    width: 100%;
    margin: 0px auto;
    padding: 0px;
  }
  
  .report-card{
    margin: 15px auto;
    width: 100%;
  }
  .report-card-body{
    flex-direction: column;
  }
  
  .lists{
    text-align: left;
    font-size: 1.5em;
    margin-bottom: 0px;
    align-self: center;
  }
  
  .lists > li {
    list-style: none;
  }

  .report-card-body-flag{
    margin: 15px 0px 25px;
    padding: 0;
    height: 100px;
    width: 200px;
    align-self: center;
  }

}

